import React from 'react'
import './Rank.css'
function Rank({name, entries}) {
  return (
    <div className='rank-component '>
      <div
      className='rank-text f5'
      >
        {`${name}, your current entry count is...`}
      </div>
      <div
      className='rank-number'
      >
        {entries}
      </div>
    </div>
  )
}

export default Rank